exports.createOgPage = ( { actions, id, component, context } ) => {
    const path = `/${config.getConfig().ogPagesDir}/${id}`
    //console.log("createOgPages path: ", path)
    const { createPage } = actions
    createPage({
        path: path,
        component: component || require.resolve('./src/templates/og-template.js'),
        context:  { 
            id: id,
            ...context,
            ogPage: true,
        }
    })

}
exports.createOgSitePage = ( { actions, component, context } ) => {
    const { createPage } = actions
    createPage({
        path: `/${config.getConfig().ogPagesDir}/site`, 
        component: component || require.resolve('./src/templates/og-site-template.js'),
        context: {
            id: 'site',
            ...context,
            ogPage: true,
        }
    })
}

