exports.config = (() => {
    const defaultConfig = {
        ogPagesDir: 'og-pages',
        width: 600,
        height: 320,
    };
  
    let currentConfig = {};
  
    return {
      init: (config) => {
        currentConfig = {
          ...defaultConfig,
          ...config,
        };
      },
      getConfig: () => {
        return currentConfig;
      },
    };
  })();